.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #f7f7f7;
  color: #222;
}

.Header {
  padding: 2rem 1rem;
  background: #fff;
  border-bottom: 1px solid #e5e5e5;
  text-align: center;
}
.Header h1 {
  margin: 0 0 0.5rem;
  font-size: 2rem;
}
.Header p {
  margin: 0 0 1rem;
  color: #555;
}

.Search {
  display: inline-flex;
  gap: 0.5rem;
}
.Search input {
  padding: 0.6rem 0.8rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  min-width: 240px;
}
.Search button {
  padding: 0.6rem 1rem;
  border: none;
  border-radius: 6px;
  background: #ff7043;
  color: #fff;
  cursor: pointer;
}

.Grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 1rem;
  padding: 1rem;
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.Card {
  background: #fff;
  border: 1px solid #e5e5e5;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.ImageWrap {
  aspect-ratio: 16/9;
  overflow: hidden;
}
.ImageWrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.CardBody {
  padding: 0.75rem 1rem 1rem;
}
.CardBody h3 {
  margin: 0 0 0.25rem;
  font-size: 1.1rem;
}
.CardBody p {
  margin: 0 0 0.75rem;
  color: #666;
}
.Button {
  display: inline-block;
  padding: 0.5rem 0.8rem;
  background: #2962ff;
  color: #fff;
  border-radius: 6px;
  text-decoration: none;
}

.Footer {
  margin-top: auto;
  padding: 1rem;
  text-align: center;
  color: #777;
}
